import { useState } from 'react';
import AddTask from "./AddTask"
import Backdrop from './Backdrop';


function Header(props) {
    const [openAdder, setopenAdder] = useState(false);

    const addMenu = () => {
        setopenAdder(true);
    }
    const exit = () => {
        setopenAdder(false)
    }
    const addtoTask = (task) => {
        props.onAdd(task)
    }
    return (
    <div>
        <header className='header'>
            <h1> {props.text} </h1>
            <button className='header-btn' onClick={addMenu}>
                Add Task 
            </button>
        </header>
        {openAdder && <AddTask onAdd={addtoTask} onCancel={exit}/>}
        {openAdder && <Backdrop onCancel={exit}/>}
    </div>
    )
}

Header.defaultProps = {
    text: 'Task List'
}
export default Header;