import { useState } from 'react';

import Modal from './Modal';
import Backdrop from './Backdrop';

function Todo(props) {
    const [openModal, setopenModal] = useState(false);

    function showModal() {
        setopenModal(true);
    }
    function closeModal() {
        setopenModal(false);
    }
    const deleteCard = () => {
        setopenModal(false);
        var myobj = document.getElementById(props.text);
        myobj.remove();
        
    }
    return (
        <div>
            <div className='card' id={props.text}>
                <h2> {props.text} </h2>
                <div className='actions'> 
                    <button className='btn' onClick={showModal}>Delete</button>
                </div>
            </div>
            {openModal && <Modal text={props.text} onCancel={closeModal} onConfirm={deleteCard}/>}
            {openModal && <Backdrop onCancel={closeModal}/>}
        </div>
    );
}

export default Todo;