import { useState } from 'react';
import Todo from './components/Todo';
import Header from './components/Header';

function App() {
  const fin =[]
  const [tasks, setTasks] = useState(['Sample Task', 'Using React'])

  
  const addTask = (task) => {
    console.log(typeof(tasks))
    setTasks([...tasks, task])
  }
  for (var i = 0; i < tasks.length; i++) {
    fin.push(<Todo text={tasks[i]} />)
  }
    return (
      <div>
        <Header onAdd = {addTask}/>
        {fin}
      </div>
    );  
}

export default App;
